// import React from 'react';
import React, { Component } from 'react';
import './test-db.scss';
import { compose } from 'recompose';

import { withFirebase } from '../firebase';
import { withAuthorization, withEmailVerification } from '../session';
import * as ROLES from '../../utils/roles';


import Slider from "react-slick";

import {
  Link
} from "react-router-dom";
import { LinkContainer } from 'react-router-bootstrap'
import * as ROUTES from '../../routes/routes.js';

import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Carousel from 'react-bootstrap/Carousel';
import Badge from 'react-bootstrap/Badge';


class HankedPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      allhanked: [],
    };
  }
  componentDidMount() {
    this.setState({ loading: true });

    this.unsubscribe = this.props.firebase
    .allhanked()
    .onSnapshot(snapshot => {
      let allhanked = [];

      snapshot.forEach(doc =>
        allhanked.push({ ...doc.data() }),
      );

      this.setState({
        allhanked,
        loading: false,
      });
    });
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  

  render() {
 
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      swipeToSlide: true,
      responsive: [
          {
              breakpoint: 1024,
              settings: {
                  slidesToShow: 3,
                  slidesToScroll: 3,
                  infinite: true,
                  dots: true
              }
          },
          {
              breakpoint: 600,
              settings: {
                  slidesToShow: 2,
                  slidesToScroll: 2,
                  initialSlide: 2
              }
          },
          {
              breakpoint: 480,
              settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1
              }
          }
      ]
  };
 
    const { allhanked, loading } = this.state;
    const HankedList = ({ allhanked }) => (
  

                       <Container className="app-container section-wrapper test-db" >
                       <Row>
                           <Col>
                               <h4>Latest Projects</h4>
                           </Col>
                           <Col>
                               <small><Link href="">View all</Link></small>
                           </Col>
                       </Row>
                       <Slider {...settings}>
                       
                            {allhanked.map(allhanked => (
                           <Card key={allhanked.id_hange}>
                               <Carousel interval={null} fade={true}>
                                   <Carousel.Item>
                                       <img
                                       className="d-block w-100"
                                       src={require("./placeholder-img.jpeg")}
                                       alt="First slide"
                                       />
                                       <Carousel.Caption>
                                       <p>First slide label</p>
                                       </Carousel.Caption>
                                   </Carousel.Item>
                                   <Carousel.Item>
                                       <img
                                       className="d-block w-100"
                                       src={require("./placeholder-img.jpeg")}
                                       alt="Third slide"
                                       />
                                       <Carousel.Caption>
                                       <p>Second slide label</p>
                                       </Carousel.Caption>
                                   </Carousel.Item>
                                   <Carousel.Item>
                                       <img
                                       className="d-block w-100"
                                       src={require("./placeholder-img.jpeg")}
                                       alt="Third slide"
                                       />
                                       <Carousel.Caption>
                                       <p>Third slide label</p>
                                       </Carousel.Caption>
                                   </Carousel.Item>
                               </Carousel>
                               <LinkContainer className="clickable" to={ROUTES.SIGN_IN}>
                                   <div>
                                       <Card.Body>
                                           <Badge pill variant="primary">ID: {allhanked.id_hange} : {allhanked.category}</Badge>
                                           <Badge pill variant="danger">{allhanked.red}</Badge>
                                           <Card.Title>{allhanked.header}</Card.Title>
                                           <Card.Text>
                                               <Row>
                                               <Col><small>{allhanked.location}</small></Col>
                                               <Col><small>5p, 11h</small></Col>
                                               </Row>
                                           </Card.Text>
                                       </Card.Body>
                                       <Card.Footer>
                                           <Row className="price-avg">
                                               <Col><h5>4 468,75 €</h5></Col>
                                           </Row>
                                       </Card.Footer>
                                   </div>
                               </LinkContainer>
                           </Card>
                                   ))}
                                   
                       </Slider>
                   </Container>
    
    

    
    
    );
    return (
  
      <div>
        <h1>Kõik hanked - admin ainult</h1>
        <p>
          Test nimekiri kõikidest test hangetest
        </p>
        {loading && <div>Loading ...</div>}

         <HankedList allhanked={allhanked} />

         
      </div>
    );
  }
}




const condition = authUser =>
  authUser && !!authUser.roles[ROLES.ADMIN];

export default compose(
  withEmailVerification,
  withAuthorization(condition),
  withFirebase,
)(HankedPage);